@media screen and (max-width: 1120px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 50vh;
    position: absolute;
    top: 100px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    visibility: hidden;
  }

  .nav-menu.active {
    visibility: visible;
    background: #1A1C1B;
    left: 0;
    opacity: 1;
    
    z-index: 1;
    margin-left: 0px !important;   
  }

  .nav-links {
    text-align: center;
    
    width: 100%;
    display: table;
    
  }

  .nav-links:hover {
    
    color: #242424;
    border-radius: 0;
  }

  .navbar-logo {
    position: absolute;
    top: -25px;
    left: 0;
    -webkit-transform: translate(25%, 50%);
            transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 10px;
    right: 0;
    -webkit-transform: translate(-100%, 60%);
            transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
   
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}


